import {ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {VaultService} from '../../services/vault.service';
import {ConfigService} from '../../services/config.service';
import {MessageBusService} from '../../services/message-bus.service';
import {Router} from '@angular/router';
import {
  LighthouseBrandListDisplayItem, LighthouseEndpointListDisplayItem,
  LighthousePortalListDisplayItem
} from '../../models/lighthouse/lighthouse-source-search';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {


  constructor(
    private vaultService: VaultService,
    public configService: ConfigService,
    private router: Router,
    private messageBus: MessageBusService,
  ) { }

  ngOnInit(): void {
    // this.vaultService.getRecordLocatorFacilities().subscribe((facilities)=>{
    //   console.log("record locator facilities", facilities)
    //   for (let facility of facilities) {
    //     this.configService.vaultProfileAddConnectedAccount(facility)
    //   }
    // })
  }

  connectAccount(pendingAccount: { brand: LighthouseBrandListDisplayItem, portal: LighthousePortalListDisplayItem, endpoint: LighthouseEndpointListDisplayItem }){
    console.log("connecting account", pendingAccount)
    this.router.navigateByUrl('dashboard/connecting', {state: new Map<string,any>([
      ["brandId", pendingAccount.brand?.id],
      ["portalId", pendingAccount.portal?.id],
      ["endpointId", pendingAccount.endpoint?.id],
      ["externalId", this.configService.systemConfig$.externalId],
    ])})
  }

  completeAccounts(){
    //publish events for all connected accounts
    this.messageBus.publishComplete()

    this.router.navigateByUrl('dashboard/complete')
  }

}
