import {ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {FormHealthSystemRequest} from '../../models/forms/form-health-system-request';
import {Router} from '@angular/router';
import {ConfigService} from '../../services/config.service';
import {VaultService} from '../../services/vault.service';
import {FormSupportRequest} from '../../models/forms/form-support-request';

@Component({
  selector: 'app-form-support-request',
  templateUrl: './form-support-request.component.html',
  styleUrls: ['./form-support-request.component.scss']
})
export class FormSupportRequestComponent implements OnInit {
  formSupportRequest: FormSupportRequest = {} as FormSupportRequest

  loading: boolean = false
  submitSuccess: boolean = false
  errorMsg: string = ""

  constructor(
    private router: Router,
    private configService: ConfigService,
    private vaultApi: VaultService,
  ) { }

  ngOnInit(): void {
    this.resetForm()
  }

  resetForm() {
    this.submitSuccess = false
    let requestForm = new FormSupportRequest()
    requestForm.email = ''
    requestForm.healthsystem_name = this.configService.searchConfig$.searchFilter?.query || ""
    requestForm.request_content = ''

    this.formSupportRequest = requestForm
  }

  submitForm() {
    this.loading = true
    this.vaultApi.requestSupport(this.formSupportRequest).subscribe((resp: any) => {
        this.loading = false
        this.submitSuccess = true
        //show success toast? close modal?
      },
      (err)=>{
        this.loading = false
        console.error("an error occurred during issue submission",err)
        this.errorMsg = err || "An error occurred while submitting your issue. Please try again later."
      })
  }

  dismiss(){
    // this.messageBus.componentNavigationSubject.next({
    //   page: ComponentNavPage.Search,
    // } as ComponentNavigationEvent)

    this.router.navigateByUrl('dashboard')
  }

}
