import {Component, Input, OnInit} from '@angular/core';
import {
  LighthouseBrandListDisplayItem, LighthouseEndpointListDisplayItem,
  LighthousePortalListDisplayItem
} from '../../models/lighthouse/lighthouse-source-search';
import {ConfigService} from '../../services/config.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-health-system-brand-details',
  templateUrl: './health-system-brand-details.component.html',
  styleUrls: ['./health-system-brand-details.component.scss']
})
export class HealthSystemBrandDetailsComponent implements OnInit {

  @Input() brand!: LighthouseBrandListDisplayItem;

  constructor(
    public configService: ConfigService,
    public router: Router
  ) {
    this.brand = configService.searchConfig$.selectedBrand!
  }

  ngOnInit(): void {
  }

  addPendingAccount(brand: LighthouseBrandListDisplayItem, portal: LighthousePortalListDisplayItem, endpoint: LighthouseEndpointListDisplayItem){
    console.log("addPendingAccount")
    this.configService.vaultProfileAddPendingAccount(brand, portal, endpoint)
    this.router.navigateByUrl('dashboard')
  }
}
