import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {VaultProfileSigninComponent} from './pages/vault-profile-signin/vault-profile-signin.component';
import {VaultProfileSigninCodeComponent} from './pages/vault-profile-signin-code/vault-profile-signin-code.component';
import {IdentityVerificationComponent} from './pages/identity-verification/identity-verification.component';
import {DashboardComponent} from './pages/dashboard/dashboard.component';
import {HealthSystemSearchComponent} from './pages/health-system-search/health-system-search.component';
import {
  HealthSystemBrandDetailsComponent
} from './pages/health-system-brand-details/health-system-brand-details.component';
import {HealthSystemConnectingComponent} from './pages/health-system-connecting/health-system-connecting.component';
import {CompleteComponent} from './pages/complete/complete.component';
import {
  FormHealthSystemRequestComponent
} from './pages/form-health-system-request/form-health-system-request.component';
import {FormSupportRequestComponent} from './pages/form-support-request/form-support-request.component';


const routes: Routes = [
  { path: 'auth/signin', component: VaultProfileSigninComponent },
  { path: 'auth/signin/code', component: VaultProfileSigninCodeComponent },
  { path: 'auth/identity/verification', component: IdentityVerificationComponent },

  { path: 'dashboard', component: DashboardComponent },
  { path: 'search', component: HealthSystemSearchComponent },
  { path: 'brand/details', component: HealthSystemBrandDetailsComponent },

  //cannot be authenticated, must be publically accessible for reconnecting
  { path: 'dashboard/connecting', component: HealthSystemConnectingComponent },
  { path: 'dashboard/complete', component: CompleteComponent },
  { path: 'form/healthsystem', component: FormHealthSystemRequestComponent },
  { path: 'form/support', component: FormSupportRequestComponent },

  { path: '', redirectTo: '/auth/signin', pathMatch: 'full' },

  //must be at bottom of list
  { path: '**', redirectTo: 'auth/signin' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
